
.Layout {
    margin: auto;
    padding: 20px;
    width: 100%;
}

.marvel-device {
    display: inline-block;
    position: relative;
    box-sizing: content-box !important;
    margin-right: 35px;
}
.marvel-device .screen {
    width: 100%;
    position: relative;
    height: 100%;
    z-index: 3;

    background: #12c2e9;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #f64f59, #c471ed, #12c2e9);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(bottom, #f64f59, #c471ed, #12c2e9);
    background: linear-gradient(to top, #f64f59, #c471ed, #12c2e9); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */



    overflow: hidden;
    display: block;
    border-radius: 1px;
    box-shadow: 0 0 0 3px #111;

    padding: 20px;
}
.marvel-device .top-bar, .marvel-device .bottom-bar {
    height: 3px;
    background: black;
    width: 100%;
    display: block;
}
.marvel-device .middle-bar {
    width: 3px;
    height: 4px;
    top: 0px;
    left: 90px;
    background: black;
    position: absolute;
}

.form-calc{
    margin-top: 100px;
}

.form-calc h1 {
    text-align: center;
    margin-bottom: 30px;
    color: white;
    text-shadow: 2px 4px 3px rgba(0,0,0,0.4);
}



/* IPHONE X */
.marvel-device.iphone-x {
    width: 375px;
    height: 812px;
    padding: 26px;
    background: #fdfdfd;
    box-shadow: inset 0 0 11px 0 black;
    border-radius: 66px;
}
.marvel-device.iphone-x .overflow {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 66px;
    overflow: hidden;
}
.marvel-device.iphone-x .shadow {
    border-radius: 100%;
    width: 90px;
    height: 90px;
    position: absolute;
    background: -webkit-radial-gradient(center,  ellipse,  rgba(0, 0, 0, 0.6) 0%,  rgba(255, 255, 255, 0) 60%);
    background: radial-gradient(ellipse at center,  rgba(0, 0, 0, 0.6) 0%,  rgba(255, 255, 255, 0) 60%);
}
.marvel-device.iphone-x .shadow--tl {
    top: -20px;
    left: -20px;
}
.marvel-device.iphone-x .shadow--tr {
    top: -20px;
    right: -20px;
}
.marvel-device.iphone-x .shadow--bl {
    bottom: -20px;
    left: -20px;
}
.marvel-device.iphone-x .shadow--br {
    bottom: -20px;
    right: -20px;
}
.marvel-device.iphone-x:before {
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    position: absolute;
    top: 5px;
    content: '';
    left: 5px;
    border-radius: 61px;
    background: black;
    z-index: 1;
}
.marvel-device.iphone-x .inner-shadow {
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    position: absolute;
    top: 10px;
    overflow: hidden;
    left: 10px;
    border-radius: 56px;
    box-shadow: inset 0 0 15px 0 rgba(255, 255, 255, 0.66);
    z-index: 1;
}
.marvel-device.iphone-x .inner-shadow:before {
    box-shadow: inset 0 0 20px 0 #FFFFFF;
    width: 100%;
    height: 116%;
    position: absolute;
    top: -8%;
    content: '';
    left: 0;
    border-radius: 200px / 112px;
    z-index: 2;
}
.marvel-device.iphone-x .screen {
    border-radius: 40px;
    box-shadow: none;
}
.marvel-device.iphone-x .top-bar, .marvel-device.iphone-x .bottom-bar {
    width: 100%;
    position: absolute;
    height: 8px;
    background: rgba(0, 0, 0, 0.1);
    left: 0;
}
.marvel-device.iphone-x .top-bar {
    top: 80px;
}
.marvel-device.iphone-x .bottom-bar {
    bottom: 80px;
}
.marvel-device.iphone-x .volume, .marvel-device.iphone-x .volume:before, .marvel-device.iphone-x .volume:after, .marvel-device.iphone-x .sleep {
    width: 3px;
    background: #b5b5b5;
    position: absolute;
}
.marvel-device.iphone-x .volume {
    left: -3px;
    top: 116px;
    height: 32px;
}
.marvel-device.iphone-x .volume:before {
    height: 62px;
    top: 62px;
    content: '';
    left: 0;
}
.marvel-device.iphone-x .volume:after {
    height: 62px;
    top: 140px;
    content: '';
    left: 0;
}
.marvel-device.iphone-x .sleep {
    height: 96px;
    top: 200px;
    right: -3px;
}
.marvel-device.iphone-x .camera {
    width: 6px;
    height: 6px;
    top: 9px;
    border-radius: 100%;
    position: absolute;
    left: 154px;
    background: #0d4d71;
}
.marvel-device.iphone-x .speaker {
    height: 6px;
    width: 60px;
    left: 50%;
    position: absolute;
    top: 9px;
    margin-left: -30px;
    background: #171818;
    border-radius: 6px;
}
.marvel-device.iphone-x .notch {
    position: absolute;
    width: 210px;
    height: 30px;
    top: 26px;
    left: 108px;
    z-index: 4;
    background: black;
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
}
.marvel-device.iphone-x .notch:before, .marvel-device.iphone-x .notch:after {
    content: '';
    height: 8px;
    position: absolute;
    top: 0;
    width: 8px;
}
.marvel-device.iphone-x .notch:after {
    background: -webkit-radial-gradient(bottom left,  circle,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    background: radial-gradient(circle at bottom left,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    left: -8px;
}
.marvel-device.iphone-x .notch:before {
    background: -webkit-radial-gradient(bottom right,  circle,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    background: radial-gradient(circle at bottom right,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    right: -8px;
}
.marvel-device.iphone-x.landscape {
    height: 375px;
    width: 812px;
}
.marvel-device.iphone-x.landscape .top-bar, .marvel-device.iphone-x.landscape .bottom-bar {
    width: 8px;
    height: 100%;
    top: 0;
}
.marvel-device.iphone-x.landscape .top-bar {
    left: 80px;
}
.marvel-device.iphone-x.landscape .bottom-bar {
    right: 80px;
    bottom: auto;
    left: auto;
}
.marvel-device.iphone-x.landscape .volume, .marvel-device.iphone-x.landscape .volume:before, .marvel-device.iphone-x.landscape .volume:after, .marvel-device.iphone-x.landscape .sleep {
    height: 3px;
}
.marvel-device.iphone-x.landscape .inner-shadow:before {
    height: 100%;
    width: 116%;
    left: -8%;
    top: 0;
    border-radius: 112px / 200px;
}
.marvel-device.iphone-x.landscape .volume {
    bottom: -3px;
    top: auto;
    left: 116px;
    width: 32px;
}
.marvel-device.iphone-x.landscape .volume:before {
    width: 62px;
    left: 62px;
    top: 0;
}
.marvel-device.iphone-x.landscape .volume:after {
    width: 62px;
    left: 140px;
    top: 0;
}
.marvel-device.iphone-x.landscape .sleep {
    width: 96px;
    left: 200px;
    top: -3px;
    right: auto;
}
.marvel-device.iphone-x.landscape .camera {
    left: 9px;
    bottom: 154px;
    top: auto;
}
.marvel-device.iphone-x.landscape .speaker {
    width: 6px;
    height: 60px;
    left: 9px;
    top: 50%;
    margin-top: -30px;
    margin-left: 0;
}
.marvel-device.iphone-x.landscape .notch {
    height: 210px;
    width: 30px;
    left: 26px;
    bottom: 108px;
    top: auto;
    border-top-right-radius: 24px;
    border-bottom-right-radius: 24px;
    border-bottom-left-radius: 0;
}
.marvel-device.iphone-x.landscape .notch:before, .marvel-device.iphone-x.landscape .notch:after {
    left: 0;
}
.marvel-device.iphone-x.landscape .notch:after {
    background: -webkit-radial-gradient(bottom right,  circle,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    background: radial-gradient(circle at bottom right,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    bottom: -8px;
    top: auto;
}
.marvel-device.iphone-x.landscape .notch:before {
    background: -webkit-radial-gradient(top right,  circle,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    background: radial-gradient(circle at top right,  transparent 0,  transparent 70%,  black 70%,  black 100%);
    top: -8px;
}

