}

.range-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}


.box-minmax{
    margin-top: 30px;
    width: 300px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-size: 20px;
    color: #FFFFFF;
}
 .box-minmax span:first-child{
      margin-left: 10px;
    }

  .range-slider {
      margin-top: 10vh;
      
  }
  .rs-range {
      margin-top: 29px;
      width: 300px;
      -webkit-appearance: none;
  }
  .rs-range:focus {
          outline: none;
      }
   .rs-range::-webkit-slider-runnable-track {
          width: 100%;
          height: 1px;
          cursor: pointer;
          box-shadow: none;
          background: #ffffff;
          border-radius: 0px;
          border: 0px solid #010101;
      }
    .rs-range::-moz-range-track {
          width: 100%;
          height: 1px;
          cursor: pointer;
          box-shadow: none;
          background: #ffffff;
          border-radius: 0px;
          border: 0px solid #010101;
      }
    
      .rs-range::-webkit-slider-thumb {
          box-shadow: none;
          border: 0px solid #ffffff;
          box-shadow: 0px 10px 10px rgba(0,0,0,0.25);
          height: 42px;
          width: 22px;
          border-radius: 22px;
          background: rgba(255,255,255,1);
          cursor: pointer;
          -webkit-appearance: none;
          margin-top: -20px;
      }
      .rs-range::-moz-range-thumb {
          box-shadow: none;
          border: 0px solid #ffffff;
          box-shadow: 0px 10px 10px rgba(0,0,0,0.25);
          height: 42px;
          width: 22px;
          border-radius: 22px;
          background: rgba(255,255,255,1);
          cursor: pointer;
          -webkit-appearance: none;
          margin-top: -20px;
    }
    .rs-range::-moz-focus-outer {
      border: 0;
      }
  
  .rs-label {
      
      position: relative;
      -webkit-transform-origin: center center;
              transform-origin: center center;
      display: block;
      width: 98px;
      height: 98px;
      background: transparent;
      border-radius: 50%;
      line-height: 30px;
      text-align: center;
      font-weight: bold;
      padding-top: 22px;
      box-sizing: border-box;
      border: 2px solid #fff;
      margin-top: 20px;
      margin-left: 100px;
      color: #fff;
      font-style: normal;
      font-weight: normal;
      line-height: normal;
      font-size: 36px;
  }
      .rs-label::after {
          content: "$";
          display: block;
          font-size: 20px;
          letter-spacing: 0.07em;
          margin-top: -2px;
      }
      
